/* CSS for Home */

.homeScreen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 32px;

  .profileWrapper {
    height: 320px;
    width: 320px;
    border-radius: 50%;
    background: conic-gradient(
      var(--theme-color),
      white,
      var(--theme-color)
    ); /* smooth transition */
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .profileBorder {
    height: 315px;
    width: 315px;
    border-radius: 50%;
    background: black; /* gap color */
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .profileImage {
    height: 300px;
    width: 300px;
    border-radius: 50%;
    background-color: transparent;
  }
  .exitWrapper {
    margin-top: 24px;
    margin-bottom: 16px;
    padding: 16px;
    text-align: center;
    quote {
      font-size: 20px;
      font-weight: 600;
      font-style: italic;
      /* color: white; */
      background-clip: "text";
      background: linear-gradient(
        90deg,
        var(--theme-color),
        #ffffff,
        var(--theme-color)
      );
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
  .introWrapper {
    max-width: 55vw;
    margin-top: 32px;

    p {
      text-align: center;
      color: white;
      font-size: 24px;
      background-clip: "text";
      background: linear-gradient(90deg, var(--theme-color), #ffffff);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
  .cursor {
    display: inline-block;
    width: 10px;
    animation: blink 1s infinite;
  }

  @keyframes blink {
    0%,
    50%,
    100% {
      opacity: 1;
    }
    25%,
    75% {
      opacity: 0;
    }
  }
}

/* CSS for About */
.aboutWrapper {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
  width: 80%;
  .stepper {
    flex: 1;
  }
}
.stepper > div {
  align-items: flex-start;
}

/* CSS for Portfolio */
.portfolioWrapper {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  width: 80%;
  justify-content: center;
  align-items: center;
}

