/* CSS for Main Screen */
.mainScreen {
  background-color: #111111;
  width: 100%;
  min-height: 100vh;
  .headingContainer {
    display: flex;
    justify-content: center;
  }
  .heading {
    position: absolute;
    transform: translateY(140%);
    margin: 0px;
    font-size: 56px;
    font-weight: 900;
    color: #ffffff;
    span {
      color: var(--theme-color);
    }
  }
  .subHeading {
    margin: 0;
    margin-top: 50px;

    position: relative;
    font-size: 110px;
    letter-spacing: 10px;
    font-weight: 800;
    color: rgba(255, 255, 255, 0.07);
  }
  .screenContent {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

/* Tablet CSS */
@media (min-width: 750px) and (max-width: 992px) {
  .aboutWrapper {
    margin-top: 24px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
  }
  .introWrapper {
    max-width: 75vw !important;
  }
  .profileWrapper {
    height: 300px !important;
    width: 300px !important;
  }
  .profileBorder {
    height: 295px !important;
    width: 295px !important;
  }
  .profileImage {
    height: 280px;
    width: 280px;
  }
  /* CSS for Main Screen */
  .mainScreen {
    .heading {
      font-size: 45px;
    }
    .subHeading {
      font-size: 90px;
      letter-spacing: 5px;
      margin-top: 40px;
    }
  }
}

/* Mobile CSS */
@media screen and (max-width: 750px) {
  .aboutWrapper {
    margin-top: 24px;
    margin-bottom: 32px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .introWrapper {
    max-width: 85vw !important;
  }
  .profileWrapper {
    height: 270px !important;
    width: 270px !important;
  }
  .profileBorder {
    height: 265px !important;
    width: 265px !important;
  }
  .profileImage {
    height: 250px !important;
    width: 250px !important;
  }
  /* CSS for Main Screen */
  .mainScreen {
    .heading {
      font-size: 36px;
      position: relative;
      transform: translateY(40%);
    }
    .subHeading {
      display: none;
    }
  }
}
